.hero {
  padding-top: 60px;
  padding-bottom: 50px;
  background-color: var(--background-color);
}

.hero-wrapper {
  @media screen and (max-width: 767px) {
    margin-right: auto;
    margin-left: auto;
    max-width: 280px;
  }
}

.hero__title {
  margin-bottom: 28px;
  @extend %title;

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-bottom: 44px;
  }

  @media screen and (min-width: 768px) {
    max-width: 537px;
    font-size: 42px;
    line-height: 1.2;
  }

  @media screen and (min-width: 1280px) {
    max-width: 500px;
    font-size: 48px;
  }
}

.hero__text {
  margin-bottom: 28px;
  font-size: 14px;
  line-height: 1.5;
  color: var(--font-color);

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
    max-width: 340px;
    font-size: 16px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 500px;
  }
}

